// Loading animation
.app-loading-animation {
  display: inline-block;
  width: 64px;
  height: 64px;
  margin: 72px 0 36px 0;
}

.app-loading-animation:after {
  content: '';
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid $color--brand--primary;
  border-color: $color--brand--primary transparent $color--brand--primary transparent;
  animation: loader-dual-ring 1.2s linear infinite;
}

@keyframes loader-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
